<template>
   <div class="about">
      <router-link to="/" class="startbtn">Add Players</router-link>
      <button @click.prevent="SelecPlayer" class="startbtn ml-1">Start</button>

      <ul class="player-list">
         <li
            v-for="(player, idx) in players"
            :key="idx"
            :class="`player-${idx} player`"
         >
            {{ player }}
         </li>
      </ul>

      <div v-if="isChallenge" class="overlay">
         <HelloWorld />
         <button @click.prevent="hideChalange" class="startbtn btn-selesai">
            Selesai
         </button>
      </div>
   </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import HelloWorld from '@/components/HelloWorld.vue';
export default {
   name: 'about',
   components: {
      HelloWorld
   },
   data() {
      return {
         currPlayer: 0,
         player: 0,
         spinTimeTotal: 0,
         spinTimeout: null,
         isChallenge: false
      };
   },
   computed: {
      ...mapState(['players']),
      ...mapGetters(['countPlayers'])
   },
   methods: {
      SelecPlayer() {
         this.spinTimeTotal = Math.random() * 500 + 500;
         this.spin();
      },
      spin(spinTime = 0) {
         if (spinTime >= this.spinTimeTotal) {
            clearTimeout(this.spinTimeout);
            setTimeout(() => {
               this.isChallenge = true;
            }, 500);
            return;
         }
         let list = document.getElementsByClassName(
            `player-${this.currPlayer}`
         );
         list[0].classList.remove('bgSpin');
         this.player >= this.countPlayers - 1
            ? (this.player = 0)
            : this.player++;
         list = document.getElementsByClassName(`player-${this.player}`);
         list[0].classList.add('bgSpin');
         this.currPlayer = this.player;
         const salt = Math.floor(Math.random() * 30) + 25;
         this.spinTimeout = setTimeout(() => {
            this.spin(spinTime + salt);
         }, 50);
      },
      hideChalange() {
         this.isChallenge = false;
      }
   }
};
</script>

