<template>
   <div class="card-challenges">
      <div :class="cardTruth" @click="rotateTruth">
         <div class="content">
            <div class="front bg-truth">
               Truth
            </div>
            <div class="back">
               <p>{{ truth }}</p>
            </div>
         </div>
      </div>

      <div :class="cardDare" @click="rotateDare">
         <div class="content">
            <div class="front bg-dare">
               Dare
            </div>
            <div class="back">
               <p>{{ dare }}</p>
            </div>
         </div>
      </div>
   </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
export default {
   name: 'HelloWorld',
   data() {
      return {
         cardTruth: {
            card: true,
            cardRotate: false
         },
         cardDare: {
            card: true,
            cardRotate: false,
            mlCard: true
         },
         truth: '',
         dare: ''
      };
   },
   computed: {
      ...mapState(['challenges']),
      ...mapGetters(['countDare', 'countTruth'])
   },
   methods: {
      rotateTruth() {
         const randomIndx = this.getRandomInt(0, this.countDare - 1);
         if (this.countTruth <= 0) {
            alert('Fatal Error!!! Refres The Page');
         }
         if (this.cardDare.cardRotate) {
            this.cardDare.cardRotate = !this.cardDare.cardRotate;
         }
         if (!this.cardTruth.cardRotate) {
            this.truth = this.challenges.truth[randomIndx];
         }
         this.cardTruth.cardRotate = !this.cardTruth.cardRotate;
      },
      rotateDare() {
         const randomIndx = this.getRandomInt(0, this.countDare - 1);
         if (this.countDare <= 0) {
            alert('Fatal Error!!! Refres The Page');
         }
         if (this.cardTruth.cardRotate) {
            this.cardTruth.cardRotate = !this.cardTruth.cardRotate;
         }
         if (!this.cardDare.cardRotate) {
            this.dare = this.challenges.dare[randomIndx];
         }
         this.cardDare.cardRotate = !this.cardDare.cardRotate;
      },
      getRandomInt(min, max) {
         min = Math.ceil(min);
         max = Math.floor(max);
         return Math.floor(Math.random() * (max - min)) + min; //The maximum is exclusive and the minimum is inclusive
      }
   }
};
</script>

